$module-title: 'gallery';

body,
.#{$module-title}__desktop {
	max-width: 100%;
	max-height: 100%;
	margin: 0;
	padding: 0;
	overflow: hidden;
	font-family: 'Saira', sans-serif;
}

select,
option {
	font-family: 'Saira', sans-serif;
	font-size: 1em;
}

h1 {
	margin-top: 0;
}

p {
	margin-bottom: 0;
}

##{$module-title}__map {
	height: 800px;
	width: 100%;
}

.#{$module-title} {
	&--hidden {
		display: none !important;
	}

	&__desktop {
		position: absolute;
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		pointer-events: none;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__window {
		z-index: 1;
	    display: flex;
	    flex-direction: column;
	    max-width: 85%;
	}

	&__content {
		padding: 1em;
		background-color: #ffffff;
		border-radius: 1em;
		overflow: hidden;
		box-shadow: 0 0 1em #000;
		max-height: 100%;
		pointer-events: auto;
	}

	&__button {
		background-color: #ffffff;
		width: 3em;
		height: 3em;
		border-radius: 50%;
		box-shadow: 0 0 1em #000;
		background-size: 50%;
		background-repeat: no-repeat;
		background-position: center;
		cursor: pointer;
		display: block;
		position: relative;
		z-index: 1;
		pointer-events: auto;

		&--fullscreen,
		&--windowed {
		    align-self: flex-start;
		    margin-left: -1.5em;
		    margin-bottom: -3em;
		}

		&--fullscreen {
			background-image: url(data:image/svg+xml;base64,);
		}

		&--windowed {
			background-image: url(data:image/svg+xml;base64,);
		}

		&--close {
			background-image: url(data:image/svg+xml;base64,);
		    align-self: flex-end;
		    margin-right: -1.5em;
		    margin-bottom: -1.5em;
		}

		&--prev {
			transform: rotateZ(180deg);
			align-self: flex-start;
			left: -1.5em;
		}

		&--next {
			align-self: flex-end;
			right: -1.5em;
		}

		.#{$module-title}__image-caption & {
			background-image: url(data:image/svg+xml;base64,);
			background-position: 1em center;
			position: absolute;
		}
	}

	&__window--hidden {
		display: none;
	}

	&__image {
		width: 100%;
		display: block;

		&-caption {
			background-color: #ffffff;
		    display: block;
		    z-index: 2;
		    position: relative;
		    margin: auto 10%;
		    border-radius: 1em;
		    padding: .5em;
		    text-align: center;
			box-shadow: 0 0 1em #000;
			display: flex;
		}

		&-container {
			padding: 0;
			background-color: rgba(0, 0, 0, .5);
		    margin-bottom: -2em;
		    height: auto;
		}
	}

	&__control {
		display: flex;
	    flex-direction: column;
	    min-height: 3em;
	}

	&__video {
		height: 100%;
	}

	&__caption-text {
		margin: auto 1.5em;
	}

	&--fullscreen {
		max-width: 100% !important;
		max-height: 100% !important;
		margin: 0 !important;
		padding: 0 !important;

		& img {
			max-width: 100% !important;
			max-height: 100% !important;
			margin: auto !important;
			width: auto !important;
			height: auto !important;
		}

		& .#{$module-title}__image-caption {
			bottom: 7em;
		}

		& .#{$module-title}__button--close,
		& .#{$module-title}__button--fullscreen,
		& .#{$module-title}__button--windowed {
			position: absolute;
			top: 1.5em;
		}

		& .#{$module-title}__button--fullscreen,
		& .#{$module-title}__button--windowed {
			left: 1.5em;
			margin-left: 0;
		}

		& .#{$module-title}__button--close {
			right: 1.5em;
			margin-right: 0;
		}

		& .#{$module-title}__image-container {
			margin: 0 !important;
			overflow: inherit !important;
			border-radius: 0;
			background-color: #000000;
			min-height: 100% !important;
			height: 100% !important;
			width: 100% !important;
			display: flex;
		}
	}


	&__navigation {
		width: 10em;
		position: absolute;
		top: 3.5em;
		left: 1.5em;
		background-color: #ffffff;
		border-radius: 1em;
		padding: .5em;
		text-align: center;
		box-shadow: 0 0 1em #000;
		pointer-events: auto;
	}

	&__select {
		width: 10em;
		margin-top: .5em;
	}
}

.debug--small {
	font-size: 8pt;
	display: block;
}
